<script>
const isModeType = (data) => data === ('pinyin' || 'traditional' || 'simplified');
let threshold = 2;
let numberWords = 3;
let mode = 'pinyin';
let randomNum = 0; //Math.floor(Math.random() * numberWords)
let round = 0;
let mastered = false;
let reveal = false;
let cache = [...Array(Mandarin.length)].map(x => 0);
let unlearnedWords = Mandarin.slice(0, numberWords);
let leftToLearn = numberWords;
let unlearnedIndex = randomNum;
const changeNumberWords = (newNum) => {
    numberWords = newNum;
    unlearnedWords = Mandarin.slice(0, newNum);
    leftToLearn = newNum;
};
const changeThreshold = (newNum) => {
    threshold = newNum;
    unlearnedWords = Mandarin.slice(0, newNum);
    leftToLearn = newNum;
};
const findNextUnlearnedIndex = (currentWords) => {
    let index = unlearnedIndex + 1 > numberWords - 1 ? 0 : unlearnedIndex + 1;
    while (cache[currentWords[index]] === threshold) {
        if (index + 1 > numberWords - 1)
            index = 0;
        else
            index += 1;
    }
    return index;
};
const answer = (answer) => {
    if (answer) {
        cache[randomNum] += 1;
        if (cache[randomNum] === threshold)
            mastered = true;
    }
    else
        cache[randomNum] = Math.max(0, cache[randomNum] - 1);
    cache = cache;
    setTimeout(() => {
        const currentWords = unlearnedWords.map(x => Mandarin.indexOf(x));
        const newUnlearned = currentWords.filter((x) => cache[x] < threshold);
        // console.log(newUnlearned.length);
        if (newUnlearned.length > 0) {
            leftToLearn = newUnlearned.length;
            randomNum = randomNum + 1 > (numberWords * (round + 1)) - 1 ? numberWords * round : randomNum + 1;
            unlearnedIndex = findNextUnlearnedIndex(currentWords);
            const newNum = Mandarin.find((word) => word === unlearnedWords[unlearnedIndex]);
            if (newNum)
                randomNum = Mandarin.indexOf(newNum);
        }
        else {
            round += 1;
            unlearnedWords = Mandarin.slice(round * numberWords, (round + 1) * numberWords);
            randomNum = round * numberWords;
            unlearnedIndex = 0;
            leftToLearn = numberWords;
        }
        mastered = false;
        reveal = false;
    }, 300);
    // console.log(cache, randomNum, unlearnedIndex)
};
</script>
  
  <main>
    <div id="info">
      <h3>ROUND: {round + 1}</h3>
      <h3>
        STUDYING 
        <select value={numberWords} on:change={e => changeNumberWords(Number(e.currentTarget.value))}>
          <option value={3}>3</option> 
          <option value={10}>10</option> 
          <option value={25}>25</option>
          <option value={50}>50</option>
          <option value={75}>75</option>
          <option value={100}>100</option>
        </select>
        WORDS PER ROUND
      </h3>
      <h3>
        NEED SCORE OF
        <select value={threshold} on:change={e => changeThreshold(Number(e.currentTarget.value))}>
          <option value={1}>1</option> 
          <option value={2}>2</option> 
          <option value={3}>3</option> 
          <option value={4}>4</option>
          <option value={5}>5</option>
          <option value={6}>6</option>
        </select>
        TO MASTER A WORD
      </h3>
      <h3>
        MODE:
        <select value={mode} on:change={e => {if (isModeType(e.currentTarget.value)) mode = e.currentTarget.value}}>
          <option value={'pinyin'}>pinyin</option> 
          <option value={'traditional'}>traditional</option> 
          <option value={'simplified'}>simplified</option>
        </select>
      </h3>
    </div>
  
    <div id="word">
      <h1>{unlearnedWords[unlearnedIndex][mode]}</h1>
      <h2 class={mastered ? 'mastered' : ''}>SCORE: {cache[randomNum]} {#if mastered}&#128170;{/if}</h2>
    </div>
  
    <div id="answer">
      {#if reveal}
        <h3>{unlearnedWords[unlearnedIndex].meaning}</h3>
        <h2>Did you get it right?</h2>
        <button on:click={() => answer(true)}>YES</button>
        <button on:click={() => answer(false)}>NO</button>
      {:else}
        <button on:click={() => reveal = true}>
          REVEAL DEFINITION
        </button>
      {/if}
    </div>
  
    <div>
      <h6>{leftToLearn} words left this round</h6>
    </div>
  </main>
  
